import React, { Component } from 'react';
import * as ons from 'onsenui'
import * as Ons from 'react-onsenui'
import LocationPage from './LocationPage'

class MainPage extends Component {
  constructor(props) {
    super(props);

    this.renderToolbar = this.renderToolbar.bind(this);
    this.renderRow = this.renderRow.bind(this);
    this.pushPage = this.pushPage.bind(this);
  }

  pushPage(navigator, id) {
    navigator.pushPage({
      component: LocationPage,
      props: {
        locations: this.props.locations,
        id: id
      }
    });
  }

  renderToolbar() {
    return (
      <Ons.Toolbar>
        <div className="left">
          <Ons.BackButton></Ons.BackButton>
        </div>
        <div className="center">
          Tourism Moose Jaw App
      </div>
      </Ons.Toolbar>
    );
  }

  renderRow(row, index) {
    return (
      <Ons.ListItem
        key={row.objectID}
        modifier={index === this.props.locations.length - 1 ? 'longdivider' : null}
        onClick={() => {
          /* actions.selectLocation(id); */
          this.pushPage(this.props.navigator, row.objectID);
        }}
        tappable
      >
        <div className = 'left'>
          <img src={row.image} className='list-item__thumbnail' />
        </div>
        <div className='center'>
          <div className='list-item__title'>
            {row.title}
          </div>
          <div className='list-item__subtitle'>
            {row.subtitle}
          </div>
        </div>
        
      </Ons.ListItem>
    );
  }

  render() {
    return (
      <Ons.Page renderToolbar={this.renderToolbar}>
        <Ons.List
          dataSource={this.props.locations}
          renderRow={this.renderRow}
          renderHeader={() => <Ons.ListHeader>Locations</Ons.ListHeader>} 
        />
      </Ons.Page>
    );
  }
}

export default MainPage;