import React, { Component } from 'react';
import * as ons from 'onsenui'
import * as Ons from 'react-onsenui'

class LocationPage extends Component {
  constructor(props) {
    super(props);
    console.log(this.props);

    this.renderToolbar = this.renderToolbar.bind(this);
    this.scanBarCode = this.scanBarCode.bind(this);
    this.getMap = this.getMap.bind(this);
  }

  renderToolbar() {
    return (
      <Ons.Toolbar>
        <div className="left">
          <Ons.BackButton></Ons.BackButton>
        </div>
        <div className="center">
          Tourism Moose Jaw App
      </div>
      </Ons.Toolbar>
    );
  }

  scanBarCode() {

  }

  getMap(locationName) {
    window.open(`https://maps.google.com/?q=${locationName.replace(' ', '+')}`);
  }

  render() {
    console.log(this.props);
    const location = this.props.locations[this.props.id];

    return (
      <Ons.Page renderToolbar={this.renderToolbar}>
        <Ons.Card>
          <div className="title right">
            {location.title}
          </div>
          <img src={location.image} alt={location.title} style={{width: '100%'}} />
          <div className="content">
            {location.description}
          </div>
          <section style={{margin: '16px'}}>
            <div style={{margin: '6px'}}>
              <Ons.Button onClick={this.scanBarCode} modifier='large'>I am here</Ons.Button>
            </div>
            <div style={{margin: '6px'}}>
              <Ons.Button onClick={() => this.getMap(location.title)} modifier='large outline'>Get directions</Ons.Button>
            </div>
          </section>
        </Ons.Card>
      </Ons.Page>
    );
  }
}

export default LocationPage;