import React, { Component } from 'react';
import * as ons from 'onsenui'
import * as Ons from 'react-onsenui'
import './App.css';
import AuthPage from './AuthPage';
import MainPage from './MainPage';
import LocationPage from './LocationPage';

const locations = [
  {
    title: "Tunnels of Moose Jaw",
    subtitle: "Guided tour",
    description: "Head beneath the streets of downtown Moose Jaw and undergo two guided theatrical tours. Relive Al Caponeâ€™s bootlegging days in the Chicago Connection tour and experience first-hand the hardships of early Chinese immigrants in the Passage to Fortune tour. We are a family friendly attraction that offers an experience like no other.",
    image: 'https://www.tunnelsofmoosejaw.com/fileadmin/template1/images/chicagoconnectionlogo-large.jpg',
    objectID: 0
  },
  {
    title: "Mac the Moose",
    subtitle: "A steel and concrete sculpture of a moose",
    description: "Mac the Moose is a steel and concrete sculpture of a moose in Moose Jaw, Saskatchewan. Formerly the World's Largest Moose, it is claimed to be North America's largest moose as well as the World's Second Largest Moose at 32 feet (9.8 m) feet tall and a weight of approximately 10 long tons (10,000 kg)",
    image: "https://i.pinimg.com/originals/7a/cb/94/7acb940429f3fe4efecb50ad26ff31f3.jpg",
    objectID: 1
  },
  {
    title: "The Casino Moose Jaw",
    subtitle: "First-class gaming",
    description: "Enjoy first-class gaming, relaxed dining and world-class entertainment at one of Western Canada's premier tourist destinations.",
    image: "https://casinomoosejaw.com/res/img/logoCasinomoosejaw.png",
    objectID: 2
  }
]

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      username: "",
      password: "",
      locations: locations,
    };

    this.handleUsernameChange = this.handleUsernameChange.bind(this);
    this.handlePasswordChange = this.handlePasswordChange.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.renderToolbar = this.renderToolbar.bind(this);
    this.renderPage = this.renderPage.bind(this);
  }

  handleUsernameChange(e) {
    this.setState({ username: e.target.value });
  }

  handlePasswordChange(e) {
    this.setState({ password: e.target.value });
  }

  handleClick() {
    if (this.state.username === 'bob' && this.state.password === 'secret') {
      ons.notification.alert('You are now signed in!');
    } else {
      ons.notification.alert('Username or password incorrect!');
    }
  }

  renderToolbar() {
    return (
    <Ons.Toolbar>
      <div className="left">
        <Ons.Icon></Ons.Icon>
      </div>
      <div className="center">
        Tourism Moose Jaw App
      </div>
    </Ons.Toolbar>
    );
  }

  renderPage(route, navigator) {
    // if (route.title === 'Auth page') {
    //   return <AuthPage nav={navigator} />
    // } else if (route.title === 'Main page') {
    //   return <MainPage locations={this.state.locations} nav={navigator} />
    // } else if (route.title === 'Location page') {
    //   return <LocationPage locations={this.state.locations} nav={navigator} />
    // }
    route.props = route.props || {};
    route.props.navigator = navigator;

    return React.createElement(route.component, route.props);
  }

  render() {
    return (
      <Ons.Navigator
        swipeable
        renderPage={this.renderPage}
        initialRoute={{
          component: AuthPage,
          props: {locations: this.state.locations},
          hasBackButton: false,
        }}
      />
    );
  }
}

export default App;
