import React, { Component } from 'react';
import * as ons from 'onsenui'
import * as Ons from 'react-onsenui'
import MainPage from './MainPage';

class AuthPage extends Component {
  constructor(props) {
    super(props);
    console.log(this.props);

    this.state = {
      username: "",
      password: "",
    };

    this.handleUsernameChange = this.handleUsernameChange.bind(this);
    this.handlePasswordChange = this.handlePasswordChange.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.renderToolbar = this.renderToolbar.bind(this);
    this.pushPage = this.pushPage.bind(this);
  }

  // pushPage(nav) {
  //   nav.pushPage({
  //     title: 'Main page',
  //     hasBackButton: true
  //   });
  // }

  pushPage(navigator) {
    navigator.pushPage({
      component: MainPage,
      props: {
        locations: this.props.locations
      }
    });
  }

  handleUsernameChange(e) {
    this.setState({ username: e.target.value });
  }

  handlePasswordChange(e) {
    this.setState({ password: e.target.value });
  }

  handleClick() {
    if (this.state.username === '1' && this.state.password === '2') {
      //ons.notification.alert('You are now signed in!');
      this.pushPage(this.props.navigator);
    } else {
      ons.notification.alert('Username or password incorrect!');
    }
  }

  renderToolbar() {
    return (
      <Ons.Toolbar>
        <div className="left">
          <Ons.Icon></Ons.Icon>
        </div>
        <div className="center">
          Tourism Moose Jaw App
      </div>
      </Ons.Toolbar>
    );
  }

  render() {
    return (
      <div className="App">
        <Ons.Page renderToolbar={this.renderToolbar}>
          <div style={{ display: 'flex', justifyContent:'center', alignItems: 'center', height: '100%'}}>
            <section style={{ textAlign: 'center'}}>
              <p>
                <Ons.Input
                  value={this.state.username}
                  onChange={this.handleUsernameChange}
                  modifier='underbar'
                  float
                  placeholder='Username' />
              </p>
              <p>
                <Ons.Input
                  value={this.state.password}
                  onChange={this.handlePasswordChange}
                  modifier='underbar'
                  type='password'
                  float
                  placeholder='Password' />
              </p>
              <p>
                <Ons.Button onClick={this.handleClick}>Sign in</Ons.Button>
                <Ons.Button modifier='large--quiet'>Don't have an account? Create one</Ons.Button>
              </p>
            </section>
          </div>
        </Ons.Page>
      </div>
    );
  }
}

export default AuthPage;